<template>
    <div class="package">
        <div v-if="isBasicTask">
            <!-- <p>基础任务内容: `服务总价:${} 总时长：${}` </p> -->
            <div>
                <span>基础任务内容：</span>
                <span class="price">(服务总价：</span>
                <span class="price">总时长：)</span>
            </div>
            <div class="task">
                <Row>
                    <Col class="task-content" span="6" v-for="(task, id) in basicTask" :key="id">{{task}}</Col>
                </Row>
            </div>
        </div>
        <div v-if="isAdditionalTasks">
            <p>附加任务内容:</p>
            <div class="task">
                <Row>
                    <Col class="task-content" span="6" v-for="(task, id) in additionalTasks" :key="id">
                    {{task}}
                    <span class="price">(价格：￥100 时长：20小时)</span>
                    </Col>
                </Row>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AdtTaskContent',
    props: {
        isBasicTask: {
            type: Boolean,
            default: true
        },
        isAdditionalTasks: {
            type: Boolean,
            default: true
        },
        basicTask: {
            type: Array,
            default: function () {
                return []
            },
        },
        additionalTasks: {
            type: Array,
            default: function () {
                return []
            },
        }
    },
    data() {
        return {
        }
    },
}
</script>

<style lang="scss" scoped>
.package {
    .price{
        color: $secondary-font-color;
    }
    .task {
        margin: 10px 0;
        padding: 15px;
        // background-color: $background-color;
        background-color: white;
        .task-content {
            margin: 5px;
        }
    }
}
</style>
